'use client';
import { useAuth } from '@/context/auth-context';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import Link from 'next/link';
import { ArrowRight, BrainCircuit, Calendar, CheckSquare } from 'lucide-react';

export default function DashboardPage() {
  const { role } = useAuth();

  const studentFeatures = [
    {
      title: "View Timetable",
      description: "Check your weekly class schedule at a glance.",
      href: "/timetable",
      icon: <Calendar className="h-8 w-8 text-primary" />,
    },
    {
      title: "Check Attendance",
      description: "Monitor your attendance percentage for each subject.",
      href: "/attendance",
      icon: <CheckSquare className="h-8 w-8 text-primary" />,
    },
  ];

  const facultyFeatures = [
    {
      title: "View Timetable",
      description: "Access your teaching schedule for the week.",
      href: "/timetable",
      icon: <Calendar className="h-8 w-8 text-primary" />,
    },
    {
      title: "Mark Attendance",
      description: "Quickly mark attendance for your classes.",
      href: "/attendance",
      icon: <CheckSquare className="h-8 w-8 text-primary" />,
    },
    {
      title: "AI Timetable Generator",
      description: "Create and optimize timetables with AI assistance.",
      href: "/ai-generator",
      icon: <BrainCircuit className="h-8 w-8 text-primary" />,
    },
  ];

  const features = role === 'student' ? studentFeatures : facultyFeatures;
  const userName = role === 'student' ? "Sam" : "Dr. Ada";

  return (
    <div className="flex flex-col gap-8">
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Welcome back, {userName}!</h1>
        <p className="text-muted-foreground">Here's a quick overview of your available actions.</p>
      </div>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
        {features.map((feature) => (
          <Card key={feature.title} className="hover:shadow-lg transition-shadow duration-300 group">
            <Link href={feature.href} className="block h-full">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-lg font-medium">{feature.title}</CardTitle>
                {feature.icon}
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground h-10">{feature.description}</p>
                <div className="flex items-center pt-4 text-primary font-semibold text-sm">
                  Go to page <ArrowRight className="ml-2 h-4 w-4 transition-transform group-hover:translate-x-1" />
                </div>
              </CardContent>
            </Link>
          </Card>
        ))}
      </div>
    </div>
  );
}
